body {
  background-color: bisque;
}

p {
  text-wrap: balance;
}

.container {
  min-width: 300px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
}

.breed-select {
  max-width: 900px;
  margin: 30px auto;
}

.cat-info {
  width: 900px;
  background-color: #f5f5f5;
  border-radius: 8px;
  margin: 0 auto;
  overflow: hidden;
}

.error {
  display: none;
}

.loader {
  color: rgba(0, 0, 0, 0);
}

.customLoader {
  width: 12px;
  height: 12px;
  color: #f5f5f5;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  animation: animloader 1s linear infinite alternate;
  display: block;
  position: relative;
}

@keyframes animloader {
  0% {
    box-shadow: -38px -12px, -14px 0, 14px 0, 38px 0;
  }

  33% {
    box-shadow: -38px 0, -14px -12px, 14px 0, 38px 0;
  }

  66% {
    box-shadow: -38px 0, -14px 0, 14px -12px, 38px 0;
  }

  100% {
    box-shadow: -38px 0, -14px 0, 14px 0, 38px -12px;
  }
}

